import { merge } from 'lodash';
import PropTypes from 'prop-types';
import ReactApexChart from 'react-apexcharts';
// material
import { Box, Card, CardHeader } from '@mui/material';
import { useTheme } from '@mui/material/styles';
// utils
import cloneDeep from 'lodash/cloneDeep';
import { BaseOptionChart } from '../../charts';

// ----------------------------------------------------------------------

const category = ['Release'];

const data = [{ data: [0] }];

RunDurationReport.propTypes = {
  categories: PropTypes.object,
  CHART_DATA: PropTypes.object
};

export default function RunDurationReport({ categories, CHART_DATA }) {
  const theme = useTheme();
  const chartOptions = merge(BaseOptionChart(), {
    colors: [theme.palette.chart.violet[0]],
    tooltip: {
      marker: { show: false },
      custom: ({ series, seriesIndex, dataPointIndex }) => {
        console.log('series', series);
        return CHART_DATA && CHART_DATA[0]
          ? `<div style="">${CHART_DATA[seriesIndex]?.data[dataPointIndex]}</div>`
          : ``;
      },
      y: {
        formatter: (seriesName) => seriesName,
        title: {
          formatter: () => ''
        }
      }
    },
    // series: CHART_DATA,
    plotOptions: {
      bar: { horizontal: true, barHeight: '28%', borderRadius: 2 }
    },
    xaxis: {
      categories: categories || category
    }
  });

  return (
    <Card>
      <CardHeader title="Run Duration Report" />
      <Box sx={{ mx: 3 }} dir="ltr">
        <ReactApexChart type="bar" series={cloneDeep(CHART_DATA) || data} options={chartOptions} height={390} />
      </Box>
    </Card>
  );
}
